import './navbarStyle.css';

function Navbar(props) {
  return (
    <nav className="navbar">
      <div className="text-logo">
        <h4>Rp's interactive website</h4>
      </div>

      <div className="nav-items">
        <div
          role="button"
          className={props.activeNav === 0 ? 'nav-active' : ''}
          onClick={() => {
            props.clickEvent(0);
          }}
        >
          Home
        </div>
        <div
          role="button"
          className={props.activeNav === 1 ? 'nav-active' : ''}
          onClick={() => {
            props.clickEvent(1);
          }}
        >
          Interactive Sorting
        </div>
        {/* <div
          role="button"
          className={props.activeNav === 2 ? 'nav-active' : ''}
          onClick={() => {
            props.clickEvent(2);
          }}
        >
          About me
        </div> */}
      </div>
    </nav>
  );
}

export default Navbar;
