import React from 'react';
import p5 from 'p5';
import SimplexNoise from 'simplex-noise';

class LandingP5 extends React.Component {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();

    this.state = { height: 0, width: 0, resized: false };

    this.getLoops = props.stopLoops;
  }

  componentDidMount() {
    this.myP5 = new p5(this.Sketch, this.myRef.current);

    const height = document.getElementById('p5js-container').clientHeight;
    const width = document.getElementById('p5js-container').clientWidth;

    this.setState({ height, width });
  }

  componentWillUnmount() {
    this.myP5.remove();
    this.myP5 = undefined;
  }

  render() {
    return <div ref={this.myRef} className="p5js" id="p5js-container"></div>;
  }

  Sketch = (p) => {
    p.windowResized = () => {
      const height = document.getElementById('p5js-container').clientHeight;
      const width = document.getElementById('p5js-container').clientWidth;

      this.setState({ height, width });
      p.resizeCanvas(this.state.width, this.state.height);

      setupRes(p);
    };

    p.setup = () => {
      p.createCanvas(this.state.width, this.state.height);
      p.frameRate(30);

      setupRes(p);
    };

    p.draw = () => {
      // For some reason causes the sketch to display correctly after being unmounted
      // TODO: FIGURE OUT WHY!!!
      if (!this.state.resized) {
        this.myP5.resizeCanvas(this.state.width, this.state.height);
        this.setState({ resized: true });
      }

      if (this.getLoops()) p.frameRate(5);
      else p.frameRate(30);

      p.background(0);
      drawDots(p);
    };
  };
}

export default LandingP5;

// ----------------P5JS functions-----------------------------

// Resultion used to calculate the amount of dots needed
let res;

// Stores the noise values of each dot
let dotsArr = [];
let dotSpacing_x;
let dotSpacing_y;

// Used for moving between the noise
let xOffset = 0;
let zOffset = 0;

// simplex noise generation
const simplex = new SimplexNoise();

function setupRes(p) {
  if (p.windowWidth < 1000) {
    res = 20;
  } else {
    res = 45;
  }

  createDots(p);
}

function createDots(p) {
  dotsArr = [];

  dotSpacing_x = p.width / res;
  dotSpacing_y = p.height / res;

  // Adds an extra line of dots to reach the ends
  res += 1;

  // Multiplier to determine the effect of moving through the noise
  const nMult = 0.05;

  for (let x = 0; x < res; x++) {
    dotsArr[x] = [];
    for (let y = 0; y < res; y++) {
      dotsArr[x][y] = simplex.noise3D((x + xOffset) * nMult, y * nMult, zOffset);
    }
  }
}

function drawDots(p) {
  // debugDots(p);

  p.strokeWeight(1);
  p.stroke(0);
  p.fill('#4895ef');

  // Halway points between dots
  // Used to draw edges
  const a = p.createVector(dotSpacing_x / 2, 0);
  const b = p.createVector(dotSpacing_x, dotSpacing_y / 2);
  const c = p.createVector(dotSpacing_x / 2, dotSpacing_y);
  const d = p.createVector(0, dotSpacing_y / 2);

  for (let x = 0; x < res - 1; x++) {
    for (let y = 0; y < res - 1; y++) {
      const decimalVal = convertToBinary(x, y);

      p.push();
      p.translate(x * dotSpacing_x, y * dotSpacing_y);
      p.beginShape();
      lookupTable[decimalVal](p, a, b, c, d);
      p.endShape(p.CLOSE);
      p.pop();
    }
  }

  xOffset += 0.01;
  zOffset += 0.005;
  setupRes(p);

  p.fill(0, 255, 0);
  p.text(`${p.floor(p.frameRate())}`, 10, 10);
}

// Converts corner dots to a binary array
// Order: Start topleft proceed clockwise
// Binary number is converted back to decimal
// to use in lookup table
function convertToBinary(x, y) {
  const p1 = dotsArr[x][y] > 0.35 ? 1 : 0;
  const p2 = dotsArr[x + 1][y] > 0.35 ? 1 : 0;
  const p3 = dotsArr[x + 1][y + 1] > 0.35 ? 1 : 0;
  const p4 = dotsArr[x][y + 1] > 0.35 ? 1 : 0;

  const binary = `${p1}${p2}${p3}${p4}`;
  return parseInt(binary, 2);
}

function debugDots(p) {
  for (let x = 0; x < res; x++) {
    for (let y = 0; y < res; y++) {
      p.stroke(p.map(dotsArr[x][y], -1, 1, 0, 255));
      p.strokeWeight(10);
      p.point(x * dotSpacing_x, y * dotSpacing_y);
    }
  }
}

// Helper function to draw vector graphs between points
function drawLine(p, a, b) {
  p.vertex(a.x, a.y);
  p.vertex(b.x, b.y);
}

const lookupTable = {
  0: (p, a, b, c, d) => {},
  1: (p, a, b, c, d) => {
    drawLine(p, d, c);
    p.vertex(0, dotSpacing_y);
  },
  2: (p, a, b, c, d) => {
    drawLine(p, b, c);
    p.vertex(dotSpacing_x, dotSpacing_y);
  },
  3: (p, a, b, c, d) => {
    drawLine(p, d, b);
    p.vertex(dotSpacing_x, dotSpacing_y);
    p.vertex(0, dotSpacing_y);
  },
  4: (p, a, b, c, d) => {
    drawLine(p, a, b);
    p.vertex(dotSpacing_x, 0);
  },
  5: (p, a, b, c, d) => {
    drawLine(p, a, d);
    p.vertex(0, dotSpacing_y);
    drawLine(p, c, b);
    p.vertex(dotSpacing_x, 0);
  },
  6: (p, a, b, c, d) => {
    drawLine(p, a, c);
    p.vertex(dotSpacing_x, dotSpacing_y);
    p.vertex(dotSpacing_x, 0);
  },
  7: (p, a, b, c, d) => {
    drawLine(p, a, d);
    p.vertex(0, dotSpacing_y);
    p.vertex(dotSpacing_x, dotSpacing_y);
    p.vertex(dotSpacing_x, 0);
  },
  8: (p, a, b, c, d) => {
    drawLine(p, a, d);
    p.vertex(0, 0);
  },
  9: (p, a, b, c, d) => {
    drawLine(p, a, c);
    p.vertex(0, dotSpacing_y);
    p.vertex(0, 0);
  },
  10: (p, a, b, c, d) => {
    drawLine(p, a, b);
    p.vertex(dotSpacing_x, dotSpacing_y);
    drawLine(p, c, d);
    p.vertex(0, 0);
  },
  11: (p, a, b, c, d) => {
    drawLine(p, a, b);
    p.vertex(dotSpacing_x, dotSpacing_y);
    p.vertex(0, dotSpacing_y);
    p.vertex(0, 0);
  },
  12: (p, a, b, c, d) => {
    drawLine(p, d, b);
    p.vertex(dotSpacing_x, 0);
    p.vertex(0, 0);
  },
  13: (p, a, b, c, d) => {
    drawLine(p, b, c);
    p.vertex(0, dotSpacing_y);
    p.vertex(0, 0);
    p.vertex(dotSpacing_x, 0);
  },
  14: (p, a, b, c, d) => {
    drawLine(p, d, c);
    p.vertex(dotSpacing_x, dotSpacing_y);
    p.vertex(dotSpacing_x, 0);
    p.vertex(0, 0);
  },
  15: (p, a, b, c, d) => {
    p.vertex(0, 0);
    p.vertex(dotSpacing_x, 0);
    p.vertex(dotSpacing_x, dotSpacing_y);
    p.vertex(0, dotSpacing_y);
  },
};
