/* eslint-disable no-loop-func */

import { Vector } from 'p5';

//#region Sketch function and resets
function createSketch(props) {
  return (p) => {
    p.windowResized = () => {
      props = props.screenChange(true);

      p.resizeCanvas(props.width, props.height);
      p.resetSketch();
    };

    p.setup = () => {
      p.createCanvas(props.width, props.height);
      p.resetSketch();
    };

    p.draw = () => {
      // For some reason causes the sketch to display correctly after being unmounted
      // TODO: FIGURE OUT WHY!!!
      if (!props.resized) {
        props = props.screenChange(true);

        p.resizeCanvas(props.width, props.height);
        p.resetSketch();
      }

      p.background(0);

      drawMethod(p, props);
    };

    p.resetSketch = () => {
      doShuffle = false;
      doSort = false;

      //TODO: Refactor to use deconstruction
      count = props.data.count;
      speed = props.data.speed;
      fastSort = props.data.fastSort;
      reverse = props.data.reverseSort;

      indexOne = -1;
      indexTwo = -1;

      // This only works because line sorting is the only 2D visualization at the moment
      if (props.data.drawType[1] === 3) useExtraDataPoints = true;
      else useExtraDataPoints = false;

      createDataPoints(props);
    };

    p.setSpeed = (newSpeed) => {
      reverse = props.data.reverseSort;
      speed = newSpeed;
    };

    p.startShuffling = async () => {
      if (!doShuffle) {
        doSort = false;

        if (busySorting) {
          setTimeout(() => {
            p.startShuffling();
          }, 10);

          return;
        }

        doShuffle = true;

        if (useExtraDataPoints) {
          const xShuffle = shuffleData(p, dataPoints, false);
          const yShuffle = shuffleData(p, dataPointsExtra, false);

          await Promise.all([xShuffle, yShuffle]);
          doShuffle = false;
          indexOne = -1;
          indexTwo = -1;
        } else {
          shuffleData(p, dataPoints, true);
        }
      }
    };

    p.startSorting = async () => {
      if (!doSort) {
        doShuffle = false;
        doSort = true;

        // References a number determined by the dropdown in footercontrols
        switch (props.data.sortType[1]) {
          case 0:
            if (useExtraDataPoints) {
              const xSort = selectionSort(dataPoints, false);
              const ySort = selectionSort(dataPointsExtra, false);

              await Promise.all([xSort, ySort]);
              resetSortVar();
            } else {
              selectionSort(dataPoints, true);
            }
            break;
          case 1:
            if (useExtraDataPoints) {
              const xSort = insertionSort(dataPoints, false);
              const ySort = insertionSort(dataPointsExtra, false);

              await Promise.all([xSort, ySort]);
              resetSortVar();
            } else {
              insertionSort(dataPoints, true);
            }
            break;
          case 2:
            if (useExtraDataPoints) {
              const xSort = bubbleSort(dataPoints, false);
              const ySort = bubbleSort(dataPointsExtra, false);

              await Promise.all([xSort, ySort]);
              resetSortVar();
            } else {
              bubbleSort(dataPoints, true);
            }
            break;
          case 3:
            if (useExtraDataPoints) {
              const xSort = mergeSort(dataPoints, false);
              const ySort = mergeSort(dataPointsExtra, false);

              await Promise.all([xSort, ySort]);
              resetSortVar();
            } else {
              mergeSort(dataPoints, true);
            }
            break;
          case 4:
            if (useExtraDataPoints) {
              const xSort = quickSort(dataPoints, false);
              const ySort = quickSort(dataPointsExtra, false);

              await Promise.all([xSort, ySort]);
              resetSortVar();
            } else {
              quickSort(dataPoints, true);
            }
            break;
          case 5:
            if (useExtraDataPoints) {
              const xSort = bucketSort(dataPoints, false);
              const ySort = bucketSort(dataPointsExtra, false);

              await Promise.all([xSort, ySort]);
              resetSortVar();
            } else {
              bucketSort(dataPoints, true);
            }

            console.log({ dataPoints, dataPointsExtra });
            break;
          default:
            if (useExtraDataPoints) {
              const xSort = bubbleSort(dataPoints, false);
              const ySort = bubbleSort(dataPointsExtra, false);

              await Promise.all([xSort, ySort]);
              resetSortVar();
            } else {
              bubbleSort(dataPoints, true);
            }
            break;
        }
      }
    };
  };
}

export default createSketch;
//#endregion

// ---------------------p5js methods--------------------
//#region Setup functions

// Determines the amount of datapoints to sort
let count = 0;

//  Stores the actual data points
let dataPoints = [];

// Used for 2D sorting only
let dataPointsExtra = [];
let useExtraDataPoints = false;

// Determines the minimum length and width of each bar
let dataHeight;
let dataWidth;

// Determines when shuffling and sorting should begin
let doShuffle = false;
let doSort = false;

function createDataPoints(props) {
  dataPoints = [];

  for (let i = 0; i < count; i++) {
    dataPoints[i] = i + 1;
  }

  dataHeight = props.height / count;
  dataWidth = props.width / count;

  if (useExtraDataPoints) {
    dataPointsExtra = [];

    for (let i = 0; i < count; i++) {
      dataPointsExtra[i] = i + 1;
    }
  }
}
//#endregion

//#region draw methods
function drawMethod(p, props) {
  // Refernce determined by drop down controls
  switch (props.data.drawType[1]) {
    case 0:
      drawDataPointsNormal(p);
      break;
    case 1:
      drawDataPointsCenter(p);
      break;
    case 2:
      drawDataPointsAngle(p);
      break;
    case 3:
      drawDataPointsLine(p);
      break;
    default:
      drawDataPointsNormal(p);
      break;
  }
}

// Draws values in a normal fashion, from bottom to top as bars
function drawDataPointsNormal(p) {
  p.rectMode(p.CORNER);
  p.stroke(0);
  for (let i = 0; i < count; i++) {
    if (i === indexOne) p.fill(0, 255, 0);
    else if (i === indexTwo) p.fill(255, 0, 0);
    else p.fill('#4895ef');

    if (extraIndex.includes(i)) p.fill(0, 0, 255);

    const point = dataPoints[i];

    p.rect(dataWidth * i, p.height, dataWidth, -dataHeight * point);
  }
}

// Draws values from the center expanding outwards
function drawDataPointsCenter(p) {
  p.rectMode(p.CENTER);
  p.stroke(0);
  for (let i = 0; i < count; i++) {
    if (i === indexOne) p.fill(0, 255, 0);
    else if (i === indexTwo) p.fill(255, 0, 0);
    else p.fill('#4895ef');

    if (extraIndex.includes(i)) p.fill(0, 0, 255);

    const point = dataPoints[i];

    p.rect(dataWidth * i + dataWidth / 2, p.height / 2, dataWidth, -dataHeight * point);
  }
}

// Draws values based on their angle (Creates a circle)
function drawDataPointsAngle(p) {
  p.stroke(255);
  p.noFill();
  p.strokeWeight(1);

  const angleIncrease = p.TWO_PI / count;

  for (let i = 0; i < count; i++) {
    if (i === indexOne) p.stroke(0, 255, 0);
    else if (i === indexTwo) p.stroke(255, 0, 0);
    else p.stroke('#4895ef');

    if (extraIndex.includes(i)) p.stroke(0, 0, 255);

    const angle = dataPoints[i] * angleIncrease + p.PI / 2;
    const newXPosAngle = i * angleIncrease;
    const newXPos = Vector.fromAngle(newXPosAngle);
    newXPos.setMag(p.height / 5);

    p.push();
    p.translate(newXPos.x + p.width / 2, newXPos.y + p.height / 2);
    p.rotate(angle);
    p.line(0, 0, 0, -p.height / 5);
    p.pop();
  }
}

function drawDataPointsLine(p) {
  p.stroke(255);
  p.noFill();
  p.strokeWeight(5);

  for (let i = 0; i < count; i++) {
    if (i === indexOne) p.stroke(0, 255, 0);
    else if (i === indexTwo) p.stroke(255, 0, 0);
    else p.stroke('#4895ef');

    const pointX = dataPoints[i] * dataWidth;
    const pointY = p.height - dataHeight * dataPointsExtra[i];
    p.point(pointX, pointY);
  }

  p.strokeWeight(1);
}
//#endregion

//#region Sorts and shuffles

// Works like a loop, but is done asyncronously to visualize the effect
// Speeds determines the delay time between swaps
// Fast sort runs through entire array before delaying
let speed;
let fastSort;

// Show the currently selected indexes in red
let indexOne = -1;
let indexTwo = -1;

// -------------shuffling-------------------
async function shuffleData(p, arr, allowSortStop = true) {
  for (let i = 0; i < arr.length; i++) {
    const rnd1 = i;
    const rnd2 = p.floor(p.random(0, i));

    indexOne = rnd1;
    indexTwo = rnd2;

    switchData(arr, rnd1, rnd2);

    if (!fastSort) {
      await new Promise((resolve) => setTimeout(resolve, speed));
    } else {
      if (i % 5 === 0) await new Promise((resolve) => setTimeout(resolve, speed));
    }

    if (!doShuffle) break;
  }

  if (allowSortStop) {
    doShuffle = false;
    indexOne = -1;
    indexTwo = -1;
  }
}

// ------------------Sorting----------------
// Used to ensure a async method is finished before reseting anything
// ONLY CHANGED BY SORTING ALGORITHMS!!!
let busySorting = false;

// Use the bubble sorting technique
async function bubbleSort(arr, allowSortStop = true) {
  busySorting = true;

  let didSwitch = true;
  let lastIndex = arr.length;
  while (didSwitch && doSort) {
    didSwitch = false;

    let lastSwitchMade = 0;

    for (let i = 0; i < lastIndex; i++) {
      indexOne = i;
      indexTwo = i + 1;

      if (testForSwitch(arr, indexOne, indexTwo)) {
        switchData(arr, indexOne, indexTwo);

        didSwitch = true;
        lastSwitchMade = indexTwo;
      }

      if (!fastSort) await new Promise((resolve) => setTimeout(resolve, speed));

      if (!doSort) break;
    }

    if (fastSort) await new Promise((resolve) => setTimeout(resolve, speed));

    lastIndex = lastSwitchMade;
  }

  if (allowSortStop) resetSortVar();
}

// Use the selection sorting technique
async function selectionSort(arr, allowSortStop = true) {
  busySorting = true;

  for (let i = 0; i < arr.length - 1; i++) {
    for (let j = i + 1; j < arr.length; j++) {
      indexOne = i;
      indexTwo = j;

      if (testForSwitch(arr, indexOne, indexTwo)) {
        switchData(arr, indexOne, indexTwo);
      }

      if (!fastSort) await new Promise((resolve) => setTimeout(resolve, speed));

      if (!doSort) break;
    }

    if (fastSort) await new Promise((resolve) => setTimeout(resolve, speed));

    if (!doSort) break;
  }

  if (allowSortStop) resetSortVar();
}

// https://en.wikipedia.org/wiki/Insertion_sort
// Insertion sorting algorithm
async function insertionSort(arr, allowSortStop = true) {
  busySorting = true;

  for (let i = 1; i < arr.length; i++) {
    let currentIndexCopy = i;
    let didSwitch = false;
    do {
      if (currentIndexCopy - 1 < 0) break;

      indexOne = currentIndexCopy - 1;
      indexTwo = currentIndexCopy;

      didSwitch = testForSwitch(arr, indexOne, indexTwo);
      if (didSwitch) {
        switchData(arr, indexOne, indexTwo);

        if (!fastSort) await new Promise((resolve) => setTimeout(resolve, speed));
      }

      currentIndexCopy -= 1;

      if (!doSort) break;
    } while (didSwitch);

    if (fastSort) await new Promise((resolve) => setTimeout(resolve, speed));

    if (!doSort) break;
  }

  if (allowSortStop) resetSortVar();
}

//Merge sort algorithm
async function mergeSort(arr, allowSortStop = true) {
  busySorting = true;

  // Merges to array rows by creating a new array row and selecting
  // the values from the two rows based on a comparison
  const mergeVals = async function (middle, left, right) {
    let leftIndex = 0;
    let rightIndex = middle - left + 1;

    // Copies values, this ensures no values are changed in a temp array
    const newArr = arr.slice(left, right + 1);
    let arrIndex = left;
    while (leftIndex <= middle - left && rightIndex < newArr.length) {
      // Checks which sub array has a value which satifies the condition (greater/smaller)
      const switchTest = testForSwitch(newArr, leftIndex, rightIndex);
      if (!switchTest) {
        indexOne = arrIndex;
        indexTwo = leftIndex + left;
        arr[arrIndex++] = newArr[leftIndex++];
      } else {
        indexOne = arrIndex;
        indexTwo = rightIndex + left;
        arr[arrIndex++] = newArr[rightIndex++];
      }

      // Timeout which results in a waiting period between swaps
      await new Promise((resolve) => setTimeout(resolve, speed));

      // Exits only the loop if sorting has been stopped
      if (!doSort) break;
    }

    // Continues filling out the rest of the array if all of the values from the sub arrays weren't used
    // These methods continue even if sorting is stopped, this ensures that each copied value is replaced back into the array
    while (leftIndex <= middle - left) {
      indexOne = arrIndex;
      indexTwo = leftIndex + left;
      arr[arrIndex++] = newArr[leftIndex++];

      // Skips the wait if sorting has been stopped
      if (doSort) await new Promise((resolve) => setTimeout(resolve, speed));
    }

    while (rightIndex < newArr.length) {
      indexOne = arrIndex;
      indexTwo = rightIndex + left;
      arr[arrIndex++] = newArr[rightIndex++];

      // Skips the wait if sorting has been stopped
      if (doSort) await new Promise((resolve) => setTimeout(resolve, speed));
    }
  };

  // Divides and conquers
  // Splits array into smaller parts
  // Also controls wheter function is fully async or waits for each half
  const mergeSorting = async function (left, right) {
    if (left >= right) return;
    const middle = Math.floor((left + right) / 2);

    if (!doSort) return;

    if (fastSort) {
      const leftHalf = mergeSorting(left, middle);
      const rightHalf = mergeSorting(middle + 1, right);

      const allPromise = Promise.all([leftHalf, rightHalf]);
      await allPromise;
    } else {
      await mergeSorting(left, middle);
      await mergeSorting(middle + 1, right);
    }

    if (!doSort) return;
    await mergeVals(middle, left, right);
  };

  await mergeSorting(0, arr.length - 1);

  if (allowSortStop) resetSortVar();
}

let extraIndex = [];

// Quick sort algorithm
async function quickSort(arr, allowSortStop = true) {
  busySorting = true;

  const partition = async (arr, left, right) => {
    let pivotIndex = Math.floor((left + right) / 2);
    extraIndex[0] = pivotIndex;
    //Gets pivot out of the way
    switchData(arr, pivotIndex, right);

    pivotIndex = right;
    let extremeRight = right - 1;
    let extremeLeft = left;

    while (extremeLeft <= extremeRight) {
      indexOne = extremeLeft;
      indexTwo = extremeRight;

      const leftCheck = testForSwitch(arr, extremeLeft, pivotIndex);
      const rightCheck = testForSwitch(arr, pivotIndex, extremeRight);

      if (leftCheck && rightCheck) {
        switchData(arr, extremeLeft++, extremeRight--);
      }

      await new Promise((resolve) => setTimeout(resolve, speed));

      if (!leftCheck) extremeLeft++;
      if (!rightCheck) extremeRight--;

      if (!doSort) break;
    }

    switchData(arr, pivotIndex, extremeLeft);

    return extremeLeft;
  };

  const doQuickSort = async (arr, left, right) => {
    if (left >= right) return;

    const pivotIndex = await partition(arr, left, right);
    if (!doSort) return;

    if (fastSort) {
      const leftIndex = doQuickSort(arr, left, pivotIndex);
      const rightIndex = doQuickSort(arr, pivotIndex + 1, right);

      const promiseAll = Promise.all([leftIndex, rightIndex]);
      await promiseAll;
    } else {
      await doQuickSort(arr, left, pivotIndex);
      await doQuickSort(arr, pivotIndex + 1, right);
    }
  };

  await doQuickSort(arr, 0, arr.length - 1);

  if (allowSortStop) resetSortVar();
}

async function bucketSort(arr, allowSortStop = true) {
  busySorting = true;

  const bucketCount = 10;
  const bucketDivision = arr.length / bucketCount;

  const buckets = [];
  //Initializes empty bucket indexes
  for (let i = 0; i < bucketCount; i++) {
    buckets[i] = 0;
  }

  //Copies starting array
  const arrCopy = arr.slice(0, arr.length);

  // Puts each value in a designated bucket
  for (let i = 0; i < arrCopy.length; i++) {
    const val = arrCopy[i];
    let bucketDestination = Math.floor((val / (arrCopy.length + 1)) * bucketCount);

    if (reverse) bucketDestination = Math.abs(bucketCount - bucketDestination - 1);

    const mainIndex = bucketDestination * bucketDivision + buckets[bucketDestination];
    arr[mainIndex] = val;

    indexOne = i;
    indexTwo = mainIndex;

    buckets[bucketDestination]++;

    await new Promise((resolve) => setTimeout(resolve, speed));

    // Resets array if being shuffled
    if (!doSort) {
      for (let i = 0; i < arrCopy.length; i++) {
        arr[i] = arrCopy[i];
      }
      break;
    }
  }

  const insertSort = async (startIndex, endIndex) => {
    for (let i = startIndex; i < endIndex; i++) {
      let currentIndexCopy = i;
      let didSwitch = false;
      do {
        if (currentIndexCopy - 1 < startIndex) break;

        indexOne = currentIndexCopy - 1;
        indexTwo = currentIndexCopy;

        didSwitch = testForSwitch(arr, indexOne, indexTwo);
        if (didSwitch) {
          switchData(arr, indexOne, indexTwo);

          await new Promise((resolve) => setTimeout(resolve, speed));
        }

        currentIndexCopy -= 1;

        if (!doSort) break;
      } while (didSwitch);

      if (!doSort) break;
    }
  };

  // Sorts a buckets based on the index passed
  const sortBucket = async (bucketIndex) => {
    const startingIndex = bucketIndex * bucketDivision;
    const endIndex = (bucketIndex + 1) * bucketDivision;

    if (!doSort) return;

    await insertSort(startingIndex, endIndex);
  };

  const promises = [];
  for (let i = 0; i < bucketCount; i++) {
    if (!doSort) break;

    if (fastSort) {
      promises.push(sortBucket(i));
    } else {
      await sortBucket(i);
    }
  }

  await Promise.all(promises);

  if (allowSortStop) resetSortVar();
}

function resetSortVar() {
  doSort = false;
  indexOne = -1;
  indexTwo = -1;
  extraIndex = [];
  busySorting = false;
}
//#endregion

let reverse = false;

function testForSwitch(arr, a, b) {
  if (reverse) {
    if (arr[a] <= arr[b]) {
      return true;
    }
  } else {
    if (arr[a] >= arr[b]) {
      return true;
    }
  }

  return false;
}

// Switches 2 data points
function switchData(arr, a, b) {
  const temp = arr[a];
  arr[a] = arr[b];
  arr[b] = temp;
}
