import './mainStyles.css';
import portrait from './Image/portrait.jpg';
import ReactTest from './landingPageAnimation';

function MainPage(props) {
  return (
    <section key={0} className="landingSection">
      <ReactTest stopLoops={props.stopLoops}></ReactTest>
      <Summary>
        <h3>Software developer</h3>
        <h4>Profile</h4>
        <p>
          I (<span>Rasmus Pieter</span>) am an <span>innovative</span> and <span>enthusiastic</span> person. I am <span>reliable</span>,{' '}
          <span>hardworking</span> and <span>trustworthy</span> with sound leadership qualities. I am willing to take on challenges, and are able to{' '}
          <span>manage</span> myself and my workload very <span>efficiently</span> to ensure my work is completed in time despite the challenges I may be faced
          with. I am able to <span>adapt</span> to <span>changes</span> effectively. These characteristics, in conjunction with my goal orientation and strong
          desire to better myself, will make me an <span>asset</span> in the <span>workplace</span>.
        </p>
        <h4>Contributions to the workforce</h4>
        <p>
          I bring <span>quality</span> standards into any organization I work with. I focus on <span>analytical objectivity</span> and <span>precision</span>,
          and make <span>decisions</span> without <span>emotional bias</span>.
        </p>
        <h4>Brief academic History</h4>
        <p>
          I <span>completed</span> my <span>school</span> career in <span>2016</span> when I matriculated. I <span>passed</span> my final exams and earned{' '}
          <span>2 distinctions</span> missing the <span>3rd</span> one by an <span>1%</span>. I was also the <span>best</span> student in the subject{' '}
          <span>IT</span> at my highschool.
        </p>
        <p>
          Following my highschool career, I applied for study at the <span>North West University</span> (<span>NWU</span>) where I got <span>accepted</span>{' '}
          into the <span>Bsc. IT</span> course. After my <span>first year</span> at NWU I got accepted into the <span>golden key</span> association by{' '}
          <span>scoring</span> in the <span>top 15%</span> of my year. Finally I <span>completed</span> the course in <span>2019</span> when I first{' '}
          <span>graduated</span>.
        </p>
        <p>
          After I completed the Bsc. IT course I proceeded and enrolled in the <span>Honours</span> degree of the same course. I got <span>accepted</span> and{' '}
          <span>completed</span> my <span>honours</span> in <span>2020</span>.
        </p>
        <h4>Project's history</h4>
        <p>
          During my years of study I have completed <span>numerous projects</span>, both for <span>marks</span> and my own <span>research/experimentation</span>
          .
        </p>
        <p>
          While this is only a <span>list</span> and <span>description</span> of the <span>signaficant</span> projects I have completed, I plan to{' '}
          <span>add</span> most of my projects (<span>those which I am able to</span>) to <span>this interactive website</span> over time.
        </p>
        <h5 className="highlight">PLEASE NOTE</h5>
        <p>
          <span>all</span> projects on this site (<span className="highlight">including the moving background</span>) is created and run <span>live</span>,
          which means that <span>some</span> projects may cause your <span>browser</span> to <span>slow</span>, or even <span>crash</span> if you set the{' '}
          <span>settings higher</span> than your <span>machine</span> is capable of <span>handling</span>. This is <span>doubly</span> true for{' '}
          <span>mobile devices</span>.
        </p>
        <h5>University projects</h5>
        <p>
          <span>Several</span> projects were required of me during my years at the university. There were <span>two</span> specific projects which were{' '}
          <span>extraordinary</span> in my 3rd year. These were both <span>group</span> projects, the <span>first</span> was to create a <span>"chat" app</span>{' '}
          simular to <span>whatsapp</span>, and the <span>second</span> was an <span>IoT</span> project for a <span>self watering plant</span>. Both of these
          projects were a huge <span>success</span>, and <span>taught</span> me <span>valuable lessons</span> on working <span>effectively</span> as an{' '}
          <span>team</span>, as well as proper <span>time mangement</span>. Finally my <span>honours</span> project was a <span>paper</span> entailing the{' '}
          <span>effect</span> of <span>ICT infrastructure</span> on <span>African countries</span>.
        </p>
        <h5>Personal projects</h5>
        <p>
          In my pursuit of <span>understanding algorithm's</span>, and learning <span>new tegnologies</span> I have created a diverse number of projects. Most
          of these <span>projects</span> which were aimed at <span>understanding algorithms</span> were created by using either <span>processing</span> or{' '}
          <span>p5js</span> libraries. These projects <span>vary</span> from <span>machine learning</span> all the way to{' '}
          <span>vizualizing simple sorting algorithms</span>. Most of these <span>processing/p5js</span> projects will either be on the site or{' '}
          <span>added</span> in the <span>future</span>.
        </p>
        <h4>Why I created this website</h4>
        <p>
          I <span>created</span> this website both as a <span>challenge</span> and an <span>oppertunity</span>. Creating these projects to run <span>live</span>{' '}
          on a <span>website</span> is something I have <span>done before</span>. The <span>challenge</span> is incorporating these projects to be{' '}
          <span>fully interactive</span> with <span>user input</span>, while <span>not leaving</span> any <span>resource leaks</span>. Also this website{' '}
          <span>teaches</span> me new <span>techniques</span> on how to use <span>CSS, transistions</span> and how to be more <span>creative</span> when
          creating webpages.
        </p>
        <p>
          I see an <span>oppertunity</span> in this website because it can give <span>potential recruiters</span> a better idea of my <span>skills</span>, as
          well as what I am <span>capable</span> of. By <span>browsing</span> through this website and it's <span>projects</span>, it will{' '}
          <span>show recruiters</span> I am <span>hardworking</span>, and I see any projects through <span>to the end</span>.
        </p>
        <div className="summary-footer">
          <h3>Contact me</h3>
          <p>
            Cell and Whatsapp: <span>071 116 1009</span>
          </p>
          <p>
            Email: <span>rpnoeth@gmail.com</span>
          </p>
        </div>
      </Summary>
    </section>
  );
}

export default MainPage;

function Summary(props) {
  return (
    <div className="summary-container">
      <div className="img-container">
        <img src={portrait} alt="RP Noeth" className="selfie" />
      </div>
      <div className="text-container">{props.children}</div>
    </div>
  );
}
