import React from 'react';
import './mainControllerStyles.css';
import Navbar from '../General/navbar';
import MainPage from '../LandingPage/mainPage';
import { CSSTransition, TransitionGroup } from 'react-transition-group';

// TODO: Refactor, this is for testing
import Sorting from '../OnlineProjects/SortingVisualization/sorting';

class MainPageController extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeNav: 0,
      stopLoops: false,
    };

    this.navClickEvent = this.navClickEvent.bind(this);
    this.getStopLoops = this.getStopLoops.bind(this);

    this.animationTime = 1000;

    this.smallScreenAlert = () => {
      if (window.innerWidth < 800) {
        alert(
          'PLEASE NOTE!\n' +
            'This website is created with the idea of running on a pc\n' +
            'The reason for this is that some of the scripts might cause your phone to run slower than normal, or might not run at all on a mobile device\n' +
            "If you wish to continue using this website on your phone, I would recommend turning your device's screen to view in landscape mode for a better viewing experience"
        );
      }
    };
  }

  componentDidMount() {
    window.addEventListener('load', this.smallScreenAlert);
  }

  componentWillUnmount() {
    window.removeEventListener('load', this.smallScreenAlert);
  }

  getStopLoops() {
    return this.state.stopLoops;
  }

  navClickEvent(index) {
    let newState = this.state.activeNav;
    newState = index;

    this.setState({ activeNav: newState, stopLoops: true });

    setTimeout(() => {
      const stopLoops = false;

      this.setState({ stopLoops });
    }, this.animationTime);
  }

  getCurrentMainPage() {
    if (this.state.activeNav === 0) {
      return <MainPage stopLoops={this.getStopLoops} />;
    } else {
      return <Sorting />;
    }
  }

  render() {
    return (
      <>
        <Navbar activeNav={this.state.activeNav} clickEvent={this.navClickEvent} />
        <main>
          <TransitionGroup>
            <CSSTransition key={this.state.activeNav} unmountOnExit classNames="fade-out-switch" timeout={this.animationTime}>
              {this.getCurrentMainPage()}
            </CSSTransition>
          </TransitionGroup>
        </main>
      </>
    );
  }
}

export default MainPageController;
